.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  display: none;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  flex-direction: column;
}

.progress {
  text-align: center;
  background: rgb(239, 236, 34);
  color: #000;
  padding: 24px;
  border-radius: 100vh;
  font-size: 16px;
  max-width: 40%;
}

.progressBar {
  display: none;
  width: 30%;
  height: 32px;
}

.okbutton {
  display: none;
  background-color: #007bff; /* Blue color */
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 4px;
  transition-duration: 0.4s;
}

.okbutton:hover {
  background-color: #0056b3; /* Darker blue */
  color: white;
}
